.ideas {
  &__container {
    position: relative;
    padding-top: 180px;
  }

  &__title {
    position: absolute;
    top: 100px;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: block;
    margin-bottom: 20px;
    width: 20.35vw;
    height: 28.2vw;
    max-width: 293px;
    max-height: 406px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: box-shadow 0.3s;
  }

  &__item:hover {
    box-shadow: 4px 4px 8px 0.01px rgba(34, 60, 80, 0.42);
    transition: box-shadow 0.3s;
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__item:nth-child(8n+1) {
    background-image: url("../img/ideas/ideas1.webp");
  }

  &__item:nth-child(8n+2) {
    background-image: url("../img/ideas/ideas2.webp");
  }

  &__item:nth-child(8n+3) {
    background-image: url("../img/ideas/ideas3.webp");
  }

  &__item:nth-child(8n+4) {
    background-image: url("../img/ideas/ideas4.webp");
  }

  &__item:nth-child(8n+5) {
    background-image: url("../img/ideas/ideas5.webp");
  }

  &__item:nth-child(8n+6) {
    background-image: url("../img/ideas/ideas6.webp");
  }

  &__item:nth-child(8n+7) {
    background-image: url("../img/ideas/ideas7.webp");
  }

  &__item:nth-child(8n+8) {
    background-image: url("../img/ideas/ideas8.webp");
  }
}

.no-webp .ideas {
  &__item:nth-child(8n+1) {
    background-image: url("../img/ideas/ideas1.jpg");
  }

  &__item:nth-child(8n+2) {
    background-image: url("../img/ideas/ideas2.jpg");
  }

  &__item:nth-child(8n+3) {
    background-image: url("../img/ideas/ideas3.jpg");
  }

  &__item:nth-child(8n+4) {
    background-image: url("../img/ideas/ideas4.jpg");
  }

  &__item:nth-child(8n+5) {
    background-image: url("../img/ideas/ideas5.jpg");
  }

  &__item:nth-child(8n+6) {
    background-image: url("../img/ideas/ideas6.jpg");
  }

  &__item:nth-child(8n+7) {
    background-image: url("../img/ideas/ideas7.jpg");
  }

  &__item:nth-child(8n+8) {
    background-image: url("../img/ideas/ideas8.jpg");
  }
}

@media screen and (max-width: 860px) {
  .ideas {
    &__container {
      display: flex;
      overflow: hidden;
    }

    &__items {
      display: flex;
      flex-wrap: nowrap;
    }

    &__item {
      display: flex;
      flex-shrink: 0;
      margin-bottom: 0;
      height: 45vw;
    }
  }
}

@media screen and (max-width: 780px) {
  .ideas {
    &__item {
      width: 49%;
      height: 60vw;
      max-width: initial;
    }
  }
}

@media screen and (max-width: 535px) {
  .ideas {
    &__item {
      height: 100vw;
    }
  }
}