.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;

  &__home-link {
    display: block;
    width: 110px;
    height: 24px;
  }

  &__svg {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__list-items {
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 40px;
  }

  &__item-link {
    display: inline-block;
    font-size: 18px;
    line-height: 21px;
    color: $light1;
  }
}

@media screen and (max-width: 780px) {
  .nav-menu {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;

    &__item:not(:last-child) {
      margin-right: 30px;
    }
  }
}

@media screen and (max-width: 580px) {
  .nav-menu {
    display: none;
    padding-top: 80px;

    &_active {
      position: fixed;
      left: 0;
      top: 0;
      bottom: auto;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: auto;
      width: 100%;
      background-color: $dark-color1;
    }

    &__home-link {
      position: absolute;
      top: 30px;
      width: calc(94% - 160px);
      height: 40px;
    }

    &__list-items {
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
      padding-bottom: 40px;
      height: 80%;
    }

    &__item {
      margin-right: 0;
      min-height: 48px;
      text-align: center;
    }

    &__item:not(:last-child) {
      margin-right: 0;
    }

    &__item-link {
      padding: 10px 0;
      font-weight: 700;
      font-size: 30px;
      line-height: 1.5;
    }
  }
}