.furniture {
  &__wrapper {
    display: grid;
    column-gap: 55px;
    grid-template-columns: 1fr 1fr;
    padding: 50px 0;
  }

  &__links-list {
    display: flex;
    flex-wrap: wrap;
    align-self: flex-start;
    width: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) &__link{
    border-right: 1px solid $dark-color2;
    height: min-content;
  }

  &__link {
    display: inline-block;
    padding: 0 4px;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    color: $dark-color2;
  }

  &__article {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  &__subtitle {
    padding: 25px 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    color: $dark-color1;
  }
}

@media screen and (max-width: 1000px) {
  .furniture {
    &__wrapper {
      grid-template-columns: 1fr;
      row-gap: 40px;
    }
  }
}

@media screen and (max-width: 800px) {
  .furniture {
    &__item {
      align-items: center;
      min-height: 48px;
    }
  }
}