.title {
  font-weight: 600;
  font-style: normal;
  font-size: calc(28px + (8 + 8 * 0.7) * ((100vw - 320px) / 1440));
  line-height: calc(35px + (14 + 14 * 0.7) * ((100vw - 320px) / 1440));
  color: #333;
}

.description {
  font-weight: 400;
  font-style: normal;
  font-size: calc(21px + (8 + 8 * 0.7) * ((100vw - 320px) / 1440));
  line-height: calc(30px + (10 + 10 * 0.7) * ((100vw - 320px) / 1440));
  color: #646464;
}

.paragraph {
  padding-top: 20px;
  font-weight: 600;
  font-style: normal;
  font-size: 20px;
  line-height: 27px;
  color: #666;
}

.button {
  padding: 10px 37px;
  width: max-content;
  height: min-content;
  font-weight: 400;
  font-style: normal;
  font-size: 29px;
  line-height: 40px;
  background-color: #fc3;
}

