html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "asd", sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 110px;
}

.burger {
  position: fixed;
  right: 3%;
  top: 20px;
  z-index: 20;
  display: none;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background-color: #fc3;
}

.burger__icon {
  position: relative;
  width: 40px;
  height: 5px;
  background-color: #fff;
}

.burger__icon::before {
  content: '';
  position: absolute;
  left: 0;
  top: -13px;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  content: '';
  position: absolute;
  left: 0;
  top: 13px;
  z-index: 30;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon_active {
  background-color: transparent;
}

.burger__icon.burger__icon_active::before {
  transform: rotate(45deg);
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.burger__icon.burger__icon_active::after {
  transform: rotate(-45deg);
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

@media screen and (max-width: 580px) {
  .burger {
    display: flex;
  }
}

.burger_disable {
  display: none;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.nav-menu__home-link {
  display: block;
  width: 110px;
  height: 24px;
}

.nav-menu__svg {
  display: block;
  width: 100%;
  height: 100%;
}

.nav-menu__list-items {
  display: flex;
}

.nav-menu__item:not(:last-child) {
  margin-right: 40px;
}

.nav-menu__item-link {
  display: inline-block;
  font-size: 18px;
  line-height: 21px;
  color: #fff;
}

@media screen and (max-width: 780px) {
  .nav-menu {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
  }
  .nav-menu__item:not(:last-child) {
    margin-right: 30px;
  }
}

@media screen and (max-width: 580px) {
  .nav-menu {
    display: none;
    padding-top: 80px;
  }
  .nav-menu_active {
    position: fixed;
    left: 0;
    top: 0;
    bottom: auto;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: auto;
    width: 100%;
    background-color: #333;
  }
  .nav-menu__home-link {
    position: absolute;
    top: 30px;
    width: calc(94% - 160px);
    height: 40px;
  }
  .nav-menu__list-items {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-bottom: 40px;
    height: 80%;
  }
  .nav-menu__item {
    margin-right: 0;
    min-height: 48px;
    text-align: center;
  }
  .nav-menu__item:not(:last-child) {
    margin-right: 0;
  }
  .nav-menu__item-link {
    padding: 10px 0;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.5;
  }
}

.home-services {
  padding-top: 110px;
  padding-bottom: 75px;
  height: 100%;
  min-height: 500px;
  background-image: linear-gradient(0.87deg, rgba(2, 55, 135, 0.9) 0.69%, rgba(81, 38, 174, 0.4736) 100.48%, rgba(38, 92, 174, 0.0102906) 100.49%, rgba(195, 195, 196, 0.00520833) 100.5%, #76A6EF 100.51%), url("../img/home-services.webp");
  background-position-x: center;
  background-position-y: 0;
  background-size: cover;
  background-repeat: no-repeat;
}

.home-services__wrap-text {
  padding-top: 40px;
  padding-bottom: 80px;
  width: 100%;
  max-width: 780px;
}

.home-services__title {
  font-weight: 700;
  font-size: 60px;
  line-height: 82px;
  color: #fff;
}

.home-services__description {
  font-weight: 400;
  font-style: normal;
  font-size: 29px;
  line-height: 40px;
  color: #fff;
}

.home-services__form {
  display: grid;
  align-content: center;
  justify-content: center;
  grid-template-columns: repeat(5, 200px);
  grid-template-rows: repeat(2, 60px);
  width: 100%;
  min-height: 175px;
  background-color: #fff;
  gap: 15px;
}

.home-services__sort-item {
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-weight: 600;
  font-style: normal;
  font-size: 18px;
  line-height: 27px;
}

.home-services__sort-item:nth-child(2) {
  grid-column: 2/6;
}

.home-services__sort-item:last-child {
  background-color: #fc3;
  transition: 0.2s;
}

.home-services__sort-item:last-child:hover {
  transform: scaleY(1.05) translateY(-2px);
  transition: transform 0.2s;
}

.home-services__option {
  width: 100px;
}

.home-services__option:nth-child(odd) {
  background-color: #fc3;
}

.no-webp .home-services {
  background-image: linear-gradient(0.87deg, rgba(2, 55, 135, 0.9) 0.69%, rgba(81, 38, 174, 0.4736) 100.48%, rgba(38, 92, 174, 0.0102906) 100.49%, rgba(195, 195, 196, 0.00520833) 100.5%, #76A6EF 100.51%), url("../img/home-services.jpg");
}

@media screen and (max-width: 1260px) {
  .home-services__form {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 60px);
    padding: 20px;
  }
  .home-services__sort-item:nth-child(2) {
    grid-column: 1/4;
    grid-row: 1/2;
  }
}

@media screen and (max-width: 780px) {
  .home-services__form {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 60px);
    padding: 20px;
  }
  .home-services__sort-item:nth-child(2) {
    grid-column: 1/3;
  }
}

@media screen and (max-width: 530px) {
  .home-services__form {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(7, 60px);
    padding: 20px;
  }
  .home-services__sort-item:nth-child(2) {
    grid-column: 1/2;
  }
}

.services-items__list-items {
  display: flex;
  justify-content: space-between;
  padding-top: 75px;
  padding-bottom: 55px;
}

.services-items__list-item {
  display: flex;
  flex-direction: column;
  width: 32%;
  transition: box-shadow 0.3s;
}

.services-items__list-item:hover {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  transition: box-shadow 0.3s;
}

.services-items__list-item:hover .services-items__link {
  background-color: #fc3;
  transition: background-color 0.3s;
}

.services-items__list-item:hover .services-items__wrap-arrow {
  background-color: #fff;
  transition: background-color 0.3s;
}

.services-items__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 45px 25px;
  width: 100%;
  height: 135px;
  background-color: #fff;
  transition: background-color 0.3s;
}

.services-items__illustration {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.services-items__wrap-arrow {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  box-shadow: 0px 11px 11px -5px rgba(34, 60, 80, 0.6);
  background-color: #fc3;
  transition: background-color 0.3s;
}

.services-items__arrow {
  width: 10px;
  height: 12px;
}

.services-items__text {
  margin-left: 25px;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  line-height: 19px;
  color: #000;
}

@media screen and (max-width: 960px) {
  .services-items__container {
    overflow: hidden;
  }
  .services-items__list-item {
    flex-shrink: 0;
    width: 49%;
  }
}

.advantages {
  padding: 55px 0;
}

.advantages__wrapper {
  display: flex;
  justify-content: space-between;
}

.advantages__description {
  display: flex;
  flex-direction: column;
  margin-right: 80px;
  width: 65%;
}

.advantages__list {
  display: flex;
  flex-direction: column;
  padding-top: 40px;
}

.advantages__item {
  display: grid;
  column-gap: 30px;
  align-items: center;
  grid-template-columns: 92px calc(100% - 92px);
  grid-template-rows: 1fr 1fr;
}

.advantages__item:not(:last-child) {
  margin-bottom: 50px;
}

.advantages__item-illustration {
  grid-column: 1/2;
  grid-row: 1/3;
  width: 92px;
  height: 92px;
}

.advantages__item-title {
  font-weight: 400;
  font-style: normal;
  font-size: 29px;
  line-height: 40px;
}

.advantages__text {
  font-weight: 400;
