    content: "";
    position: absolute;
    left: 0;
    top: 10px;
    display: block;
    border: 1px solid #fff;
    width: 100%;
    height: 48px;
  }
  .footer__country-item::after {
    content: "";
    position: absolute;
    right: 15px;
    top: 12px;
    display: block;
    margin: 15px auto;
    border: 15px solid;
    border-color: #ccc transparent #ccc transparent;
    border-bottom: 0;
    width: 0;
    height: 0;
    transition: border 0.3s;
  }
  .footer__town-list {
    flex-direction: column;
    margin-left: 15px;
    padding-top: 15px;
  }
  .footer__town-item {
    border: 1px solid #fff;
    padding: 7px 20px;
    width: 100%;
    height: 48px;
  }
  .hiden {
    height: max-content;
  }
  .hiden::after {
    content: "";
    position: absolute;
    right: 15px;
    top: 12px;
    display: block;
    margin: 15px auto;
    border: 15px solid;
    border-color: #ccc transparent #ccc transparent;
    border-top: 0;
    width: 0;
    height: 0;
    transition: border 0.3s;
  }
